exports.push([module.id, ".dropdown-width[_v-3c82f26f]{width:500px}.dropdown-li[_v-3c82f26f]{width:50%}.pd20[_v-3c82f26f]{padding:20px}.mt20[_v-3c82f26f]{margin-top:20px}", "", {"version":3,"sources":["/./src/components/idc/room_connect/RoomConnect.vue.style"],"names":[],"mappings":"AAsKA,6BACA,WAAA,CACA,AAEA,0BACA,SAAA,CACA,AAEA,mBACA,YAAA,CACA,AAEA,mBACA,eAAA,CACA","file":"RoomConnect.vue","sourcesContent":["<!-- 机房连通性 -->\r\n<template>\r\n    <div>  \r\n        <form class=\"form-horizontal clearfix form-search\">\r\n            <div class=\"col-sm-3\">\r\n                <div class=\"form-group input-box\">\r\n                    <label class=\"col-sm-4 control-label\">机房名称：</label>\r\n                    <div class=\"col-sm-8\">\r\n                        <v-select :value.sync=\"param.idc\" :options=\"idcs\" placeholder=\"请选择\" :search=\"true\">\r\n                        </v-select>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n            <div class=\"col-sm-3\">\r\n                <div class=\"form-group input-box\">\r\n                    <label class=\"col-sm-4 control-label\">连通类型：</label>\r\n                    <div class=\"col-sm-8\">\r\n                        <v-select :value.sync=\"param.connType\" :options=\"connTypes\" placeholder=\"请选择\">\r\n                        </v-select>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </form>\r\n        <div class=\"text-center btn-operate\">\r\n            <button type=\"button\" class=\"btn btn-default\" @click=\"refresh\">\r\n                查询\r\n            </button>\r\n            <button type=\"button\" class=\"btn btn-default\" @click=\"$broadcast('showConnect')\">\r\n                机房打通\r\n            </button>\r\n        </div>\r\n        <div class=\"text-center table-title\">\r\n            查询结果\r\n        </div>\r\n        <div class=\"table-box\">\r\n            <table class=\"table table-hover table-bordered\">\r\n                <thead>\r\n                    <tr>\r\n                        <th v-for=\"title in titles\" v-text=\"title\"></th>\r\n                    </tr>\r\n                </thead>\r\n                <tbody>\r\n                    <tr v-for=\"list in tableList\" v-if=\"tableList.length !== 0\" v-show=\"tableList.length !== 0\">\r\n                        <td :title=\"list.roomFrom\" v-text=\"list.roomFrom\"></td>\r\n                        <td :title=\"list.roomTo\" v-text=\"list.roomTo\"></td>\r\n                        <td :title=\"list.connType\" v-text=\"list.connType\"></td>\r\n                        <td>\r\n                            <button type=\"button\" class=\"btn btn-default set-btn\" @click=\"$broadcast('showConfirm', list.id)\">\r\n                                切断\r\n                            </button>\r\n                        </td>\r\n                    </tr>\r\n                    <tr class=\"text-center\" v-show=\"tableList.length === 0\">\r\n                        <td :colspan=\"titles.length\">暂无数据</td>\r\n                    </tr>\r\n                </tbody>\r\n                <tfoot> \r\n                    <tr>\r\n                        <td :colspan=\"titles.length\">\r\n                            <boot-page v-ref:page :async=\"true\" :lens=\"lenArr\" :page-len=\"pageLen\" :url=\"url\" :param=\"param\"></boot-page>\r\n                        </td>\r\n                    </tr>\r\n                </tfoot>\r\n            </table>\r\n            <spinner id=\"spinner-box\" :size=\"md\" :fixed=\"false\" \r\n                 text=\"数据加载中，请稍后...\" v-ref:spinner>\r\n            </spinner>\r\n        </div>\r\n        <connect-modal></connect-modal>\r\n        <confirm-modal></confirm-modal>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nimport { spinner } from 'vue-strap'\r\nimport bootPage from '../../global/BootPage.vue'\r\nimport connectModal from './SetConnect.vue'\r\nimport confirmModal from '../../global/Confirm.vue'\r\nimport vSelect from '../../global/Select.vue'\r\nimport { getRoomConnect } from '../../../vuex/action.js'\r\nimport { idcs, connTypes } from '../../../vuex/getters.js'\r\n\r\nexport default {\r\n    data () {\r\n        return {\r\n            titles: ['机房一', '机房二', '连通类型', '操作'],\r\n            tableList: [],\r\n            lenArr: [10, 50, 100],\r\n            pageLen: 5,\r\n            url: '/idc/room_connect_query/',\r\n            param: {\r\n                idc: '',\r\n                connType: ''\r\n            }\r\n        }\r\n    },\r\n    methods: {\r\n\r\n        // 刷新数据\r\n        refresh () {\r\n            this.$refs.spinner.show()\r\n            this.$refs.page.refresh()\r\n        },\r\n\r\n        // 切断\r\n        disconnect (id) {\r\n            this.$http({\r\n                url: '/idc/room_connect_delete/',\r\n                method: 'POST',\r\n                data: {\r\n                    id: id\r\n                }\r\n            })\r\n            .then(response => {\r\n                if (response.data.code === 200) {\r\n                    this.refresh()\r\n\r\n                    this.$dispatch('show-success')\r\n                } else {\r\n                    this.$dispatch('show-error')\r\n                }\r\n            })\r\n        }\r\n    },\r\n    components: {\r\n        bootPage,\r\n        vSelect,\r\n        spinner,\r\n        connectModal,\r\n        confirmModal\r\n    },\r\n    vuex: {\r\n        actions: {\r\n            getRoomConnect\r\n        },\r\n        getters: {\r\n            idcs,   // 获取机房位置\r\n            connTypes\r\n        }\r\n    },\r\n    ready () {\r\n        this.getRoomConnect()\r\n        this.$refs.spinner.show()\r\n    },\r\n    events: {\r\n\r\n        // 获取表格数据\r\n        'data' (param) {\r\n            this.tableList = param.data\r\n            this.$refs.spinner.hide()\r\n        },\r\n\r\n        // 刷新表格\r\n        'refresh' () {\r\n            this.refresh()\r\n        },\r\n\r\n        // 确认切断\r\n        'confirm' (param) {\r\n            this.disconnect(param)\r\n        }\r\n    }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.dropdown-width {\r\n    width: 500px;\r\n}\r\n\r\n.dropdown-li {\r\n    width: 50%;\r\n}\r\n\r\n.pd20 {\r\n    padding: 20px;\r\n}\r\n\r\n.mt20 {\r\n    margin-top: 20px;\r\n}\r\n</style>\r\n"],"sourceRoot":"webpack://"}]);

